@tailwind base;
@tailwind components;
@tailwind utilities;

.zak-quick-settings-item-icon {
	svg {
		fill: #2563EB;
	}
}

.zak-feature-icon {
	svg {
		fill: #7A7A7A;
	}
}

.zak-learn-more {
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 2px;
		right: -6px;
		width: 1px;
		height: 100%;
		background: #000;
		opacity: 0.1;
	}
}

.zak-setting-tab {
	display: flex;
	gap: 24px;
	background: #fff;

	.components-tab-panel__tabs {
		display: flex;
		flex-direction: column;
		flex-basis: 30%;
		background: #FAFAFC;
		border-right: 1px solid #E1E1E1;

		button {
			border-bottom: 1px solid #E1E1E1;

			&.is-active {
				border-right: 0;
				background: #FFFFFF;

				&:after {
					height: 100%;
					width: 2px;
				}

				&:before {
					height: 100%;
					width: 1px;
					right: -1px;
					background: #FFFFFF;
					top: 0;
					left: unset;
				}
			}

		}
	}

	.components-tab-panel__tab-content {
		width: 100%;
	}
}

.zak-setting-learn-more {
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 2px;
		right: -6px;
		width: 1px;
		height: 100%;
		background: #000;
		opacity: 0.1;
	}
}

#render_dashboard_page {
	a {
		outline: 0;
		box-shadow: none;
	}
}

.zak-setting-label {
	margin-bottom: 16px;

	label {
		font-size: 18px;
		font-weight: 600;
	}
}

.header__menu {
	li{
		&.active {
			a {
				position: relative;
				color: #3858e9;


				&:after {
					content: "";
					position: absolute;
					bottom: -1px;
					right: 0;
					width: 100%;
					height: 2px;
					background: #3858e9;
				}
			}
		}

		a {
			padding: 25px 8px;

			&:hover {
				color: #3858e9;
			}
		}
	}
}
